import React from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import './App.css';
import { Login } from './components/Login';
import { Logout } from './components/Logout';
import { gapi } from 'gapi-script';
import { useEffect } from 'react';
import { PostList } from './components/PostList';
import { Post } from './components/Post';
import { Comments }   from './components/Comments';

const clientId = '573641497027-te6g7e93nq3aakkcfepibr6f3rhqffcn.apps.googleusercontent.com';

function App() {
  useEffect(() => {
    const initClient = () => {
      gapi.client.init({
        clientId: clientId,
        scope: ''
      });
    };
    gapi.load('client:auth', initClient);
  });

  const isLoggedIn = sessionStorage.getItem('userProfile') != null;
  return (
    <Router>
    {!isLoggedIn && !window.location.href.includes('login')  && <Navigate to='/login'/>}
    <div className="App">
      
        {isLoggedIn && <Logout />}
        <Routes>
          <Route path='/login' element={<Login/>} />
          <Route exact path='/' element={<PostList/>} />
          <Route path='/posts' element={<PostList/>} />
          <Route path='/posts/:postId' element={<Post/>} />
      </Routes>

    </div>
    </Router>
  )
};

export default App 