import React, { useState, useEffect } from 'react';

const post = "https://dummyjson.com/posts";

const getPostListHtml = async () => {
    const response = await fetch(post);
    const jsonResponse = await response.json();
    console.log(jsonResponse);
    const arrayOfLists = jsonResponse.posts.map(
        record => {
            const postId = `/posts/${record.id}`;
            return <li>
            <a href={postId}>{record.title}</a>
            </li>;
        }
      )
      return arrayOfLists;
};



function PostList() {
    const [apiResponse, setApiResponse] = useState("*** now loading ***");

    useEffect(() => {
        getPostListHtml().then(
            result => setApiResponse(result));
    }, []);

    return (
        <div className='postlists'>
            <h1>Posts List</h1>
            <ul><p>{apiResponse}</p></ul>
        </div>
    );
}

export {PostList}  ;